@import '../../../app/styles/variables';
@import '../../../app/styles/typography';




.abadge {
    margin: 0.1vw;
    border: 1px solid;
    border-radius: 18px;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    // width: 100%; // Установите фиксированную или процентную ширину по необходимости
    // max-width: 200px; // Пример максимальной ширины

    p.title {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        width: 100%; // Заполняет полностью ширину .abadge
    }
}

.small {
    padding: 8px;
    height: 14px;

    // width: 96.6354px; // Примерный размер для маленького бэйджа
}

.medium {
    padding: 11px;
    height: 27px;
    // width: 200px; // Примерный размер для среднего бэйджа
    width: 120px !important;
}

.big {
    padding: 15px;
    height: 34px;
    width: 200px; // Примерный размер для большого бэйджа
}

$colors: (
    'green': $accountant,
    'blue': $btx-blue,
    'fiolet': $company-pro,
    'orange': $budget-big,
    'light': $btx-card-bckgrnd,
    'dark': $base-text,
    'april': $link-color,
    'warning': $btn-color-danger,
    'danger': $btn-color-error,
    'success': $btx-green
);

// Генерация стилей в зависимости от цвета и активности
@each $color-name, $color-value in $colors {
    .abadge--#{$color-name} {
        border: 1px solid $color-value;

        &.active {
            background-color: $color-value;
            color: white; // Предполагаем, что текст белый на активном фоне
        }

        &.inactive {
            background-color: transparent;
            color: $color-value; // Цвет текста совпадает с цветом границы
        }
        &.withIcone{
            padding-right: 1.8px !important;
        }


       
    }
}

.title {

    @extend .text__small__bold;
    margin: 0;
    display: inline-block;
}

.iconeDone{
    background-color:  none !important;
}