@import '../../../../app/styles/variables';
@import '../../../../app/styles/typography';
@import '../../../../app/styles/mixins';

span {
    @extend .text__small__bold;
}

.sale_badge,
.sale_badge__active {
    width: 130px !important;
    height: 25px !important;
    border-radius: 14px !important;
    border: 1px solid $accountant !important;
    padding: 2px !important;

}

.sale_badge {
    background-color: rgb(255, 255, 255) !important;
    color: $accountant !important;
    // width: 76px !important;
    margin: 3px !important;
    max-width: 100%;
    /* Ограничиваем ширину, чтобы текст не выходил за пределы родительского элемента */
    white-space: nowrap;
    /* Убираем переносы строк */
    overflow: hidden;
    /* Скрываем текст, выходящий за границы элемента */
    text-overflow: ellipsis;

}

.sale_badge__active {

    background-color: $accountant !important;
    border: $accountant;
}

.sale_presentations {
    margin-top: 7px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
}

.itemWrapper {
    border: 1px rgb(234, 247, 249) solid;
    border-radius: 11px;
    padding:  6px;
    margin: 3px 0px;
    width: 100% !important;
    min-width: 100% !important;
    // @include flexbox($direction: column,
    //     $justify: flex-start,
    //     $align-items: start);
    @include flexbox($direction: row,
    $justify: space-between,
    $align-items: center);

}


.actionsWrapper {
    width: 100% !important;
    min-width: 100% !important;
    @include flexbox($direction: row,
        $justify: flex-end,
        $align-items: center);
}

.itemTitle{
    @extend .text__link ;
    margin-bottom: 0px;
}